@charset "UTF-8";
/* Default styles */
@import 'google-fonts.css';
:root {
  --yale-blue: hsla(212, 72%, 26%, 1);
  --berkeley-blue: hsla(215, 66%, 22%, 1);
  --oxford-blue: hsla(213, 72%, 16%, 1);
  --powder-blue: hsla(209, 32%, 66%, 1);
  --mint-cream: hsla(111, 24%, 94%, 1);
  --phthalo-blue: hsla(243, 76%, 27%, 1);
  --black-pearl: hsla(230, 70%, 5%, 1);
  --lavender-web: hsla(248, 54%, 89%, 1);
  --moonstone: hsla(185, 84%, 38%, 1);
  --moonstone-dark: hsl(200, 84%, 38%);
  --russian-violet: hsla(295, 100%, 15%, 1);
  --chinese-violet: hsla(269, 9%, 45%, 1);
  --btn-grey: hsla(240, 3%, 12%, 1);
  --dark-black: hsl(210, 50%, 1%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-weight: normal;
  color: var(--mint-cream);
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

body {
  color: var(--mint-cream);
  min-height: 800px;
  max-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  transition: background 0.25s ease-in-out;
  overflow-x: clip;
  max-width: 100vw;
  background: var(--dark-black) url("/media/stars-001.png");
}
body::-webkit-scrollbar {
  width: 0.35em;
}
body::-webkit-scrollbar-track {
  background: var(--black-pearl);
  border-left: 1px solid hsla(111, 24%, 94%, 0.1);
}
body::-webkit-scrollbar-thumb {
  background: var(--lavender-web);
}

h1 {
  font-family: "Bunken Tech Sans SC Wide W01Bd";
  text-transform: uppercase;
}

h2 {
  font-family: "Bunken Tech Sans SC Wide W01Bd";
}

body {
  font-family: "Montserrat", sans-serif;
}

a {
  font-family: "Montserrat", sans-serif;
}

p {
  font-weight: 300;
}

h1 {
  font-size: 2.489rem; /* 39.84px */
}

h2 {
  font-size: 1.728rem; /* 33.12px */
}

h3 {
  font-size: 1.44rem; /* 27.68px */
}

h4 {
  font-size: 1.2rem; /* 23.04px */
}

small {
  font-size: 0.833rem; /* 13.28px */
}

header {
  bottom: 1px solid hsla(111, 24%, 94%, 0.1);
  padding: 8px 1em 1em;
  position: absolute;
  width: 100%;
  z-index: 4;
}
@media (max-width: 768px) {
  header {
    background: hsla(230, 70%, 5%, 0.5);
    border-bottom: 1px solid hsla(111, 24%, 94%, 0.1);
    backdrop-filter: blur(2px);
  }
  header nav {
    justify-content: initial;
  }
  header .command {
    margin-left: auto;
  }
  header .burger-menu {
    display: flex;
  }
  header .burger-btn {
    display: block;
  }
  header .nav-links {
    position: absolute;
    gap: 8px;
    padding: 0 1em 10px 1em;
    top: 70px;
    right: -10px;
    background: hsla(230, 70%, 5%, 0.95);
    border: 1px solid hsla(111, 24%, 94%, 0.15);
    border-radius: 1.5em 0 1.5em 1.5em;
    flex-direction: column;
    z-index: 5;
    overflow: clip;
    height: 0px;
    max-height: 190px;
    opacity: 0;
    transition: opacity 0.15s linear, height 0.25s ease-in-out;
  }
  header .nav-links:hover > .link {
    opacity: 0.5;
  }
  header .nav-links .link:hover {
    opacity: 1;
  }
  header .link {
    transition: 0.15s ease-in-out;
  }
  header .link .ico-nav {
    display: block;
  }
}
@media (max-width: 475px) {
  header .burger-menu {
    display: flex;
    margin-left: auto;
  }
  header .link.order-link {
    display: flex;
  }
  header .command {
    display: none;
  }
  header .burger-btn:checked ~ .nav-links {
    height: 400%;
    max-height: 250px;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
  position: relative;
}
nav a {
  text-transform: uppercase;
}

.logo {
  display: flex;
  height: max-content;
  z-index: 5;
}

.ico-planet {
  height: 45px;
  width: auto;
}
.ico-planet img {
  width: 100%;
  height: 100%;
}

.link {
  padding: 10px 0 0;
  z-index: 5;
  display: flex;
  gap: 10px;
  align-items: center;
}
.link a {
  letter-spacing: 1px;
}
@media (min-width: 1024px) {
  .link a :nth-child(1) {
    transition: 0.25s all ease-in-out 0s;
  }
  .link a:hover :nth-child(1) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(2) {
    transition: 0.25s all ease-in-out 0.05s;
  }
  .link a:hover :nth-child(2) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(3) {
    transition: 0.25s all ease-in-out 0.1s;
  }
  .link a:hover :nth-child(3) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(4) {
    transition: 0.25s all ease-in-out 0.15s;
  }
  .link a:hover :nth-child(4) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(5) {
    transition: 0.25s all ease-in-out 0.2s;
  }
  .link a:hover :nth-child(5) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(6) {
    transition: 0.25s all ease-in-out 0.25s;
  }
  .link a:hover :nth-child(6) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(7) {
    transition: 0.25s all ease-in-out 0.3s;
  }
  .link a:hover :nth-child(7) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(8) {
    transition: 0.25s all ease-in-out 0.35s;
  }
  .link a:hover :nth-child(8) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(9) {
    transition: 0.25s all ease-in-out 0.4s;
  }
  .link a:hover :nth-child(9) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(10) {
    transition: 0.25s all ease-in-out 0.45s;
  }
  .link a:hover :nth-child(10) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(11) {
    transition: 0.25s all ease-in-out 0.5s;
  }
  .link a:hover :nth-child(11) {
    text-shadow: white 0px 0px 5px;
  }
  .link a :nth-child(12) {
    transition: 0.25s all ease-in-out 0.55s;
  }
  .link a:hover :nth-child(12) {
    text-shadow: white 0px 0px 5px;
  }
}
.link .ico-nav {
  display: none;
  padding: 2px 0 0;
  filter: invert(94%) sepia(3%) saturate(391%) hue-rotate(66deg) brightness(105%) contrast(91%);
}
.link.order-link {
  display: none;
}

.nav-links {
  display: flex;
  gap: 1.75em;
}

.command a {
  padding: 8px 12px;
  border-radius: 1.75em;
  border: 1px solid hsla(111, 24%, 94%, 0.75);
  transition: 0.25s ease-in-out background;
}
.command a:hover {
  background: hsl(211, 93%, 27%);
}

.burger-btn {
  display: none;
  position: absolute;
  right: 1px;
  width: 30px;
  height: 30px;
  top: 12px;
  z-index: 5;
  opacity: 0;
}

.burger-menu {
  display: none;
  flex-direction: column;
  gap: 6px;
  width: 32px;
  margin: 9px 0 0 1em;
  cursor: pointer;
}
.burger-menu .line {
  flex-grow: 1;
  height: 3px;
  border-radius: 2px;
  background: var(--mint-cream);
}
.burger-menu .double {
  display: flex;
  gap: 4px;
}
.burger-menu .double :nth-child(1) {
  width: 55%;
  transition: 0.25s width ease-in-out;
}
.burger-menu .double :nth-child(2) {
  width: 10%;
  transition: 0.25s width ease-in-out;
}

#toggle-off {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100lvh;
  z-index: 4;
}

.burger-btn:checked ~ #toggle-off {
  display: block;
}
.burger-btn:checked ~ .nav-links {
  height: 300%;
  opacity: 1;
}
.burger-btn:checked ~ .burger-menu .double :nth-child(1) {
  width: 25%;
}
.burger-btn:checked ~ .burger-menu .double :nth-child(2) {
  width: 75%;
}

footer {
  max-width: 100vw;
  text-align: left;
  line-height: 25px;
  padding: 0 0 1em;
  margin-top: 6em;
  position: relative;
  background: hsla(0, 0%, 0%, 0.5);
  backdrop-filter: blur(2px);
  box-shadow: -4px 0px 5px #000, -4px 0px 20px #000, -6px 0px 30px #000, -6px 0px 15px #000 inset, 6px 0px 20px #000 inset;
}
footer a {
  font-size: 1rem;
}
@media (max-width: 1280px) {
  footer .deco.ship {
    top: -25px;
  }
  footer .deco.spatio {
    width: 250px;
  }
}
@media (max-width: 1024px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: initial;
    bottom: 0;
    width: 125px;
  }
  footer .deco.spatio {
    width: 150px;
  }
  footer .get-in-touch {
    max-width: 80%;
  }
  footer .copyright {
    font-weight: 200;
    font-size: 0.9rem;
  }
}
@media (max-width: 768px) {
  footer .get-in-touch {
    max-width: 90%;
  }
}
@media (max-width: 640px) {
  footer .get-in-touch  {
    flex-direction: column;
    text-align: center;
    gap: 1em;
  }
  footer .adress {
    order: 1;
  }
  footer .tel {
    order: 2;
  }
  footer .mail {
    order: 3;
  }
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 12px;
    width: 150px;
  }
  footer .deco.spatio {
    right: 24px;
    width: 150px;
  }
}
@media (max-width: 475px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 1em;
    width: 100px;
  }
  footer .deco.spatio {
    top: -15%;
    right: 1em;
    width: 100px;
  }
}

.separation {
  height: 2px;
  border-radius: 4px;
  width: 50%;
  min-width: 200px;
  margin: 0 auto calc(3em + 12px);
  background: var(--lavender-web);
  position: relative;
  top: -1px;
}

.get-in-touch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 60%;
  min-width: 280px;
  margin: 0 auto;
  text-transform: uppercase;
}
.get-in-touch a {
  font-weight: 200;
}
.get-in-touch ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 1em;
}
.get-in-touch li {
  width: max-content;
}
.get-in-touch .adress {
  pointer-events: none;
}

.social-media {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 2em 0 1em;
}

.ico {
  transition: transform ease-in-out 0.25s;
  cursor: pointer;
}
.ico:hover {
  transform: scale(1.1) translateY(-5px);
}

.copyright {
  text-align: center;
}

.deco {
  position: absolute;
  top: -75px;
  height: auto;
  pointer-events: none;
  z-index: 1;
}
.deco img {
  width: 100%;
  height: 100%;
}
.deco.ship {
  left: clamp(5px, 10%, 50px);
  top: -25px;
  width: 200px;
  animation: flying 1.5s ease infinite;
}
.deco.spatio {
  right: clamp(5px, 10%, 50px);
  width: 200px;
  animation: floating 5s ease infinite;
}

.get-to-top {
  position: absolute;
  top: -115px;
  right: 1em;
  filter: invert(100%);
  opacity: 0.7;
}

#hero {
  height: calc(100vh + 50px);
  width: 100vw;
  display: grid;
  align-items: center;
  background: url(../../src/media/bg-004-1.webp);
  box-shadow: inset 0px -35px 50px #000;
}
#hero .content {
  min-width: 300px;
  width: 50%;
  margin: 0 10%;
  z-index: 2;
}
#hero .content h1 {
  max-width: 15ch;
  margin: 0 0 8px;
}
#hero .content p {
  max-width: 50ch;
  line-height: 24px;
}
@media (max-width: 1280px) {
  #hero h1 {
    font-size: 2.389rem;
  }
  #hero .bg-h.spatio {
    right: 15%;
  }
  #hero .bg-h.planet {
    right: -10%;
  }
}
@media (max-width: 1024px) {
  #hero h1 {
    font-size: 2rem;
    width: 17ch;
  }
  #hero .bg-h.spatio {
    right: 10%;
    height: 50vh;
  }
  #hero .bg-h.planet {
    right: -25%;
    height: 80vh;
  }
}
@media (max-width: 768px) {
  #hero .bg-h.spatio {
    top: 40%;
    right: -5%;
    height: 50vh;
  }
  #hero .bg-h.planet {
    right: -45%;
    height: 80vh;
  }
}
@media (max-width: 640px) {
  #hero {
    padding: 1em;
  }
  #hero h1 {
    font-size: clamp(1.2rem, 6vw, 2.5rem);
    width: 15ch;
  }
  #hero .content {
    margin: 0 auto;
  }
  #hero .content p {
    max-width: none;
    width: 100%;
  }
  #hero .bg-h {
    display: none;
  }
}

.c-t-a {
  display: flex;
  gap: 1.5em;
  margin: 2em 0 0;
}
.c-t-a > * {
  border: 1px solid white;
  padding: 8px 42px 8px 16px;
  border-radius: 1.75em;
  position: relative;
  transition: 0.25s ease-in-out background;
}
.c-t-a > *:hover > span {
  transform: translateY(-2px) rotate(55deg);
}
.c-t-a > *:hover {
  background: hsl(211, 93%, 27%);
}
.c-t-a span {
  position: absolute;
  right: 6px;
  top: 2px;
  filter: invert(1);
  transition: 0.25s ease-in all;
}

.bg-h {
  position: absolute;
  height: 70vh;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}
.bg-h.spatio {
  right: 30%;
  filter: brightness(70%);
  animation: floating-home 7s ease infinite;
}
.bg-h.planet {
  right: 8%;
  height: 80vh;
  filter: brightness(75%);
}
.bg-h img {
  height: 100%;
  width: 100%;
}

#reason {
  display: flex;
  gap: 2em;
  align-items: center;
  height: 100vh;
}

.fact {
  max-width: 40ch;
  min-height: 225px;
  padding: 1.75em;
  border-radius: 1.75em;
  position: relative;
  background: hsla(230, 70%, 5%, 0.75);
  backdrop-filter: blur(5px);
  border: 1px solid hsla(111, 24%, 94%, 0.5);
}
.fact::after {
  content: "";
  position: absolute;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 85%;
  background: var(--dark-black);
  z-index: -1;
}
.fact h2 {
  width: max-content;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}
.fact h3 {
  font-weight: 600;
  text-align: center;
  margin: 0 0 8px;
}
.fact p {
  text-align: justify;
  line-height: 25px;
}
@media (max-width: 475px) {
  .fact {
    min-height: 260px;
    margin-bottom: 3em;
  }
}

.pagination {
  display: flex;
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
}
.pagination > div {
  height: 10px;
  width: 10px;
  margin: 0 4px;
  background: var(--lavender-web);
  border-radius: 50%;
  transition: 0.25s width ease-in, 0.25s border-radius ease-in;
  cursor: pointer;
}

.navigation div {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.navigation div img {
  filter: drop-shadow(#040716 0px 0px 2px);
}
.navigation .left {
  left: -30px;
}
.navigation .right {
  right: -30px;
}

[slide-step] > .wrap {
  display: none;
}

[slide-step="1"] > .slides-1,
[slide-step="2"] > .slides-2,
[slide-step="3"] > .slides-3,
[slide-step="4"] > .slides-4,
[slide-step="5"] > .slides-5,
[slide-step="6"] > .slides-6,
[slide-step="7"] > .slides-7,
[slide-step="8"] > .slides-8,
[slide-step="9"] > .slides-9,
[slide-step="10"] > .slides-10,
[slide-step="11"] > .slides-11,
[slide-step="12"] > .slides-12 {
  display: block;
}

[slide-step="1"] > .pagination :nth-child(1),
[slide-step="2"] > .pagination :nth-child(2),
[slide-step="3"] > .pagination :nth-child(3),
[slide-step="4"] > .pagination :nth-child(4),
[slide-step="5"] > .pagination :nth-child(5),
[slide-step="6"] > .pagination :nth-child(6),
[slide-step="7"] > .pagination :nth-child(7),
[slide-step="8"] > .pagination :nth-child(8),
[slide-step="9"] > .pagination :nth-child(9),
[slide-step="10"] > .pagination :nth-child(10),
[slide-step="11"] > .pagination :nth-child(11),
[slide-step="12"] > .pagination :nth-child(12) {
  width: 20px;
  border-radius: 16px;
}

.wrap {
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(15px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@media (max-width: 1280px) {
  #reason .portfolios {
    gap: 1.75em;
    max-width: none;
    margin: 0;
  }
  #reason .portfolios a {
    width: 250px;
    padding: 12px;
  }
  #reason .spatio-stand {
    left: -95px;
  }
}
@media (max-width: 1024px) {
  #reason {
    flex-direction: column;
    gap: 10%;
    justify-content: center;
  }
  #reason h2 {
    font-size: 1.3rem;
  }
  #reason .portfolios {
    padding: 1.75em;
    width: 100%;
  }
  #reason .portfolios a {
    width: 250px;
    padding: 12px;
  }
}
@media (max-width: 640px) {
  #reason {
    padding: 1em;
    overflow: hidden;
  }
  #reason .portfolio {
    text-align: center;
    width: 100%;
    margin: 2.5em 0 0;
    position: relative;
  }
  #reason .portfolio h2 {
    margin: 0 0 1em;
    letter-spacing: 1px;
    position: absolute;
    top: -45px;
    left: 50%;
    transform: translateX(-50%);
    width: max-content;
  }
  #reason .portfolios {
    border-radius: 1.75em;
    display: flex;
    max-width: none;
    margin: 0 auto;
    padding: 1.75em 0;
  }
  #reason .portfolios a {
    width: 250px;
    border-radius: 2em;
    border: 1px solid #fff;
    text-align: center;
    letter-spacing: 1px;
  }
  #reason .spatio-stand {
    display: none;
  }
  #reason .fact {
    max-width: none;
    width: 90vw;
    min-width: 290px;
    padding: 1.75em 1em;
    border-radius: 1.5em;
  }
  #reason .fact::after {
    top: -3px;
    height: 4px;
    width: 85%;
  }
  #reason .fact h2 {
    top: -15px;
  }
  #reason .fact h3 {
    font-size: 1.1rem;
  }
}

.portfolio {
  text-align: center;
  width: 65%;
  margin: 0 auto;
  position: relative;
}
.portfolio h2 {
  margin: 0 0 1em;
  letter-spacing: 1px;
  position: absolute;
  top: -45px;
  left: 50%;
  transform: translateX(-50%);
  width: max-content;
}
.portfolio .spatio-stand {
  bottom: -4px;
  left: -4px;
  position: absolute;
  height: 350px;
  width: auto;
}
.portfolio .spatio-stand img {
  width: 100%;
  height: 100%;
}

.portfolios {
  background: hsla(230, 70%, 5%, 0.31) url("/media/home/bg-port.webp") top center/cover no-repeat;
  border-radius: 2em;
  border: 2px solid hsla(111, 24%, 94%, 0.1);
  display: flex;
  gap: 2.5em;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0 auto;
  padding: 4.5em 0;
}
.portfolios a {
  display: block;
  width: 275px;
  height: max-content;
  border-radius: 2em;
  border: 2px solid #fff;
