  filter: invert(100%);
  opacity: 0.7;
  transition: 0.25s opacity ease-in-out;
  cursor: pointer;
  background: hsla(0, 0%, 100%, 0.375);
  width: 30px;
  border-radius: 50%;
  aspect-ratio: 1/1;
  box-shadow: #fff 0px 0px 10px, #fff 0px 0px 10px, #fff 0px 0px 10px;
}
.nav-btn img {
  width: 100%;
  height: 100%;
  scale: 1.1;
  position: relative;
  bottom: -3px;
}
.nav-btn:hover {
  opacity: 1;
}
.nav-btn.next {
  right: 0;
  transform: rotate(90deg);
}
.nav-btn.pre {
  left: 0;
  transform: rotate(-90deg);
}
@media (max-width: 475px) {
  .nav-btn {
    top: 87%;
  }
  .nav-btn.next {
    right: 3%;
    transform: rotate(90deg);
  }
  .nav-btn.pre {
    left: 3%;
    transform: rotate(-90deg);
  }
}

[active-site] > div {
  display: none;
}

[active-site="1"] > .site-1,
[active-site="2"] > .site-2 {
  display: initial;
}

.prev, .descrip {
  animation: siteIn 0.5s ease-out;
}

@keyframes siteIn {
  from {
    transform: translateX(25px);
  }
  to {
    transform: translateX(0);
  }
}
#about {
  margin-top: 55px;
  width: 100vw;
  position: relative;
}
@media (max-width: 1024px) {
  #about {
    margin-top: 85px;
  }
  #about .part {
    width: 90%;
  }
  #about .deco-more.spatios {
    scale: 0.7;
  }
  #about .deco-more.planets.one {
    scale: 0.9;
  }
}
@media (max-width: 475px) {
  #about {
    margin-bottom: 30px;
    overflow: hidden;
  }
}

.part {
  margin: 0 auto 3em;
  width: 80%;
  min-width: none;
  text-align: justify;
  min-height: 100vh;
  display: flex;
  align-items: center;
}
.part .text {
  background: hsla(230, 70%, 5%, 0.7);
  padding: 2em;
  border-radius: 2em;
}
.part h1 {
  margin-bottom: 1em;
  text-align: center;
  font-size: clamp(1.5rem, 3vw, 2.5rem);
}
.part p {
  line-height: 25px;
}

.team {
  margin-top: 1.75em;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3em;
}

.person {
  width: max-content;
  text-align: center;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  font-size: 1.2rem;
}
.person h4 {
  font-size: 1.2rem;
  font-weight: 600;
}
.person .picture {
  text-align: center;
  height: 200px;
  aspect-ratio: 1/1;
  margin: 0 auto 8px;
  border-radius: 50%;
  overflow: hidden;
  background: var(--dark-black) url(/media/stars-001.png);
  backdrop-filter: blur(5px);
  border: 2px solid var(--oxford-blue);
  box-shadow: 0px 0px 10px var(--chinese-violet);
}
.person .picture img {
  width: 125%;
  height: 125%;
  object-fit: cover;
  object-position: center;
}
.person .picture.jenna img {
  width: 130%;
  height: 130%;
  object-position: -35px 0;
}
.person .picture.axel img {
  object-position: -20px 0;
}
.person .picture.andreas img {
  width: 200%;
  height: 200%;
  object-position: -90px 30px;
}

.deco-more {
  position: absolute;
  top: 0;
  filter: brightness(40%);
}
.deco-more img {
  width: 100%;
  height: 100%;
}
.deco-more.spatios {
  z-index: -1;
  right: 0;
  scale: 0.8;
}
.deco-more.planets.one {
  z-index: -3;
  right: -30%;
}
.deco-more.planets.two {
  z-index: -2;
  left: 0;
  bottom: -25%;
  top: initial;
  width: clamp(350px, 30vw, 1000px);
  aspect-ratio: 1/1;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (min-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
@keyframes floating {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 8px);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes floating-home {
  0% {
    transform: translateY(-49%) rotate(2deg);
  }
  50% {
    transform: translateY(-50%) rotate(0);
  }
  100% {
    transform: translateY(-49%) rotate(2deg);
  }
}
@keyframes flying {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(4deg);
  }
  100% {
    transform: rotate(0);
  }
}
section {
  animation: page-entering 0.25s ease-in-out forwards;
}

@keyframes page-entering {
  from {
    opacity: 0;
  }
}
@keyframes light {
  0% {
    text-shadow: none;
  }
  100% {
    text-shadow: var(--lavender-web) 0px 0px 5px;
  }
}